<template>
  <div class="hello">
    <el-button
      type="primary"
      icon="el-icon-plus"
      size="small"
      @click="addDialogVisible = true"
      >添加用户</el-button
    >
    <el-table :data="users" style="width: 100%" border stripe>
      <el-table-column prop="id" label="#"> </el-table-column>
      <el-table-column prop="name" label="姓名"> </el-table-column>
      <el-table-column prop="age" label="年龄"> </el-table-column>
      <el-table-column prop="email" label="邮箱"> </el-table-column>
      <el-table-column label="操作" width="250">
        <template slot-scope="scope">
          <!-- 编辑 -->
          <el-button
            type="info"
            icon="el-icon-setting"
            size="small"
            @click="getUserById(scope.row.id)"
            >编辑</el-button
          >
          <!-- 删除 -->
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="small"
            @click="delUser(scope.row.id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="1"
      :page-size="size"
      layout="total, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <!-- 添加框 -->
    <el-dialog title="添加用户" :visible.sync="addDialogVisible" width="50%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="姓名">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="年龄">
          <el-input v-model="form.age"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑框 -->
    <el-dialog title="编辑用户" :visible.sync="editDialogVisible" width="50%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="ID" align="left">
          {{ form.id }}
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="年龄">
          <el-input v-model="form.age"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUser">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      users: [],
      total: 0,
      size: 0,
      addDialogVisible: false,
      editDialogVisible: false,
      form: {
        id: 0,
        name: "",
        age: "",
        email: "",
      },
    };
  },
  created() {
    this.handleCurrentChange(1);
  },
  methods: {
    // getAllUser: function () {
    //   this.axios
    //     .get("/users",{
    //       params: {
    //         currentPage: 1
    //       }
    //     })
    //     .then((res) => {
    //       console.log(res.data.data);
    //       this.total = res.data.data.total;
    //       this.size = res.data.data.size;
    //       this.users = res.data.data.records;
    //     })
    //     .catch((error) => {
    //       throw error;
    //     });
    // },

    // 处理分页
    handleCurrentChange: function (currentPage) {
      this.axios
        .get("/user", {
          params: {
            currentPage: currentPage,
          },
        })
        .then((res) => {
          console.log(res.data.data);
          this.total = res.data.data.length;
          this.size = res.data.data.length;
          this.users = res.data.data;
        })
        .catch((error) => {
          throw error;
        });
    },

    // 删除用户
    delUser: function (id) {
      this.$confirm("删除用户？")
        .then(() => {
          this.axios
            .delete("/user/" + id)
            .then((res) => {
              console.log(res);
              if (res.data.code == 1200) {
                this.$message.success(res.data.message);
                this.handleCurrentChange(this.currentPage);
              } else {
                this.$message.error(res.data.message);
              }
            })
            .catch((error) => {
              throw error;
            });
        })
        .catch(() => {
          console.log("no");
        });
    },

    // 添加用户
    addUser: function () {
      console.log(this.form);
      this.$confirm("添加用户？")
        .then(() => {
          this.axios
            .post("/user", this.form)
            .then((res) => {
              if (res.data.code == 1200) {
                this.$message.success(res.data.message);
                this.form = {};
                this.addDialogVisible = false;
                this.handleCurrentChange(this.currentPage);
              } else {
                this.$message.error(res.data.message);
              }
            })
            .catch((error) => {
              this.$message.error("添加失败");
            });
        })
        .catch(() => {
          console.log("no");
        });
    },

    // 获取用户详情
    getUserById: function (id) {
      this.axios
        .get("/user/" + id)
        .then((res) => {
          if (res.data.code == 1200) {
            console.log(res.data);
            this.form = res.data.data;
            this.editDialogVisible = true;       
          } else {
            this.$message.error(res.data.message);
          }
        })
        .catch((error) => {
          throw error;
        });
    },

    // 提交编辑
    editUser(){
      console.log(this.form);
      this.$confirm("编辑用户？")
        .then(() => {
          this.axios
            .put("/user", this.form)
            .then((res) => {
              if (res.data.code == 1200) {
                this.$message.success(res.data.message);
                this.form = {};
                this.editDialogVisible = false;
                this.handleCurrentChange(this.currentPage);
              } else {
                this.$message.error(res.data.message);
              }
            })
            .catch((error) => {
              this.$message.error("修改失败");
            });
        })
        .catch(() => {
          console.log("no");
        });
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width: 80%;
  margin: 0 auto;
}

.el-table {
  margin: 15px 0px;
  background: #f0f9eb;
}
</style>



